.wrapper {
    margin: 5% auto
}

/* -- Events filters -- */

.sport-filters {
    margin: 4% 0;
}

.sport-filters .selector {
    display: inline-block;
    margin: 0.3em;
    padding: 0.6em;
    border-radius: 1em;
    text-align: center;
    font-weight: bold;
}

.sport-filters .selected {
    background-color: #4a65cc;
    color: #d7e3f4
}

.sport-filters .unselected {
    background-color: #a0b3e5;
    color: #7582a9;
    text-decoration: line-through;
}

/* -- Events table --*/

.events-table {
    margin-top: 5%;
    margin-bottom: 5%;
}

.events-table div {
    padding: 0.2em;
}

.events-table .table-header {
    position: sticky;
    top: 0;
    background-color: #a0b3e5;
    border-radius: 0.5em;
}

.events-table .table-header .event-name {
    padding: 0.5em 0.5em 0.5em 2em;
}

.events-table .table-header .event-name h6 {
    font-size: 1.3em;
    font-weight: bold;
}

.events-table .table-header .counter {
    border: 0;
    padding: 0.5em 0;
    background-color: #4a65cc;
    border-radius: 1em;
    color: #d7e3f4;
    vertical-align: middle;
    font-size: 1.1em;
    font-weight: bold;
}

.events-table .line {
    margin: 0;
    padding: 0;
    border-radius: 0.5em;
}

.events-table .line:hover {
    background-color: rgba(179, 196, 234, 0.79);
}

.events-table .line .level-1 h6 {
    font-weight: 650;
    padding-left: 1em;
}

.events-table .line .level-2 {
    padding-left: 2em;
}

.events-table .line .level-3 {
    padding-left: 3em;
}

.events-table .line .level-4 {
    padding-left: 4em;
}

.events-table .table-body .line .blockLog-table {
    padding-left: 1em;
}

.events-table .table-body .line .blockLog-table .warning {
    display: inline-block;
    padding: 0.5em;
    border-radius: 0.3em;
    background-color: #ffe7ab;

}

.events-table .line .counters {
    display: flex;
}

.events-table .line .counters .counter {
    min-width: 9%;
    margin: 0 auto;
    text-align: center;
    border-left: 1px solid #96a8d7;
}

.events-table .line .counters .counter p {
    margin: 0;
    padding: 0;
}
